* {
  box-sizing: border-box;
}

html {
  flex-direction: column;
  align-items: center;
}

body {
  color: var(--deep-gray);
  background: linear-gradient(135deg, var(--color1) 25%, var(--color2) 50%, var(--color3) 75%, var(--color4) 100%);
  flex-direction: column;
  align-items: center;
  width: 100%;
  height: 100%;
  margin: 0;
  font-family: Segoe UI, sans-serif;
  font-size: 16px;
  display: flex;
  overflow-x: hidden;
}

h1, h2, h3 {
  margin: 0;
  padding-top: 1rem;
  padding-bottom: 1rem;
}

h1, h2, h3, h4, h5, h6 {
  color: var(--color4);
}

a {
  color: var(--primary-500);
}

a:hover {
  color: var(--primary-400);
}

p {
  margin: 0;
  padding: 0;
}

ul {
  align-items: left;
  flex-flow: column;
  padding-left: 1.2rem;
  list-style-type: square;
  display: flex;
}

nav {
  align-items: center;
  justify-content: last baseline;
  z-index: 1000;
  border-bottom: solid 1px var(--color3);
  background: var(--color4);
  flex-flow: column wrap;
  gap: 0;
  width: 100vw;
  height: auto;
  padding: 1.5rem 0;
  transition: opacity .3s;
  display: flex;
  position: relative;
  top: 0;
}

nav:hover {
  box-shadow: 0px 0px 2px var(--color2);
  transition: all .3s ease-in-out;
}

nav ul {
  flex-flow: row;
  align-items: center;
  gap: 1rem;
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.nav-list {
  text-align: center;
  white-space: nowrap;
  align-items: center;
  display: flex;
}

nav a {
  text-align: center;
  white-space: nowrap;
  color: var(--color2);
  min-width: 6rem;
  height: 6%;
  padding-top: 1rem;
  font-size: 1rem;
  text-decoration: none;
  display: inline-block;
}

nav a:hover {
  color: var(--color2);
  font-weight: bold;
  transition: all .2s ease-in-out;
  transform: scale(1.05);
}

.title {
