}

.search-container button:hover {
  color: #000;
  background-image: linear-gradient(to bottom, var(--primary-500), var(--primary-400));
  transition: all .25s ease-in-out;
}

.dark-mode-toggle {
  white-space: nowrap;
  color: var(--color2);
  flex-direction: row;
  align-items: center;
  width: auto;
  margin: 0 auto;
  padding-bottom: .5rem;
  display: flex;
