  font-size: 1.3rem;
}

.news-summary {
  text-align: justify;
  margin-top: .5rem;
  margin-bottom: .5rem;
}

.top {
  position: relative;
}

.container {
  gap: 1rem;
  display: flex;
  position: relative;
}

.aside {
  float: left;
  background-color: #ffffff46;
  border: 1px solid #aeaeae;
  width: 20%;
  padding: 1rem;
}

main .book-item {
  text-align: center;
  background-color: #fff;
