  align-items: center;
  width: 100%;
  max-width: 724px;
  margin: 0 auto 1rem;
  padding: .5rem;
}

main .review-item:hover {
  transition: all .2s ease-in-out;
  box-shadow: 24px 24px 24px #00000080;
}

.book-item h3, .book-item img, .book-item a {
