  box-shadow: 24px 24px 24px #00000080;
}

.times-wire-img {
  width: 130px;
}

main .movie-item {
  text-align: center;
  background-color: #fff;
  border: 1px solid #aeaeae;
  border-radius: 4px;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  width: 100%;
  max-width: 724px;
  margin: 0 auto 1rem;
  padding: .5rem;
}

main .movie-item:hover {
  transition: all .2s ease-in-out;
  box-shadow: 24px 24px 24px #00000080;
}

.movie-item p {
  text-align: justify;
}

footer {
  text-align: center;
  color: var(--color3);
  background: var(--color2);
  border-bottom: 1px solid #aaa;
  flex-flow: column wrap;
  justify-content: center;
  align-items: center;
  gap: 0;
  width: 100vw;
  height: 8rem;
  padding: 1rem;
  display: flex;
}

footer p {
  margin: .5rem;
}

footer img {
  width: 12rem;
  height: auto;
}

#mostpoptitle {
  margin: 0 auto 1rem;
  padding: 1.5rem;
}

.marquee {
  white-space: nowrap;
  background-color: #343843;
  width: 100vw;
  overflow: hidden;
  transform: translateY(-1rem);
}

.marquee-container {
  flex-direction: column;
  display: flex;
}

.marquee p {
  color: #fff;
  width: 100vw;
  height: auto;
  margin: .5rem;
  animation: 200s linear infinite marqueeText;
  display: inline-block;
}

.photo-marquee {
  -webkit-backdrop-filter: blur(10px);
  background-image: linear-gradient(to bottom, var(--primary-500), var(--primary-400));
  border: 1px solid #fff3;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 160px;
  display: flex;
  overflow: hidden;
}

.photo-marquee img {
  opacity: 1;
  border-radius: 5px;
  width: 100vw;
  margin: .5rem;
  animation: 40s linear infinite marqueeImages;
  display: inline-block;
}

.photo-marquee img:hover {
  transition: all .3s ease-in-out;
  box-shadow: 24px 24px 24px #00000080;
}

@keyframes marqueeText {
  0% {
    transform: translateX(110%);
  }

  100% {
    transform: translateX(-1700%);
  }
}

@keyframes marqueeImages {
  0% {
    transform: translateX(1700%);
  }

  100% {
    transform: translateX(-1700%);
  }
}

body.dark-mode {
  color: #fff;
  background: #333;
  transition: all .3s ease-in-out;
}

.dark-mode #mostpoptitle, .dark-mode .section-title {
