  border: none;
  transition: all .2s ease-in-out;
}

.dark-mode .book-item h3, .dark-mode .review-item h3, .dark-mode .times-wire-container h3, .dark-mode .movie-item h3, .dark-mode .articles h3 {
  color: #fff;
}

.dark-mode .review-item, .dark-mode .times-wire-container {
  color: #fff;
  background: linear-gradient(90deg, #444 0%, #555 30%, #666 60%, #777 90%);
  border: none;
  transition: all .2s ease-in-out;
}

.dark-mode .articles h2 {
  color: #fff;
}

footer.dark-mode {
  color: #fff;
  background: linear-gradient(90deg, #444 0%, #555 30%, #666 60%, #777 90%);
}

